button {
  height: 2.5rem;
  border-radius: 0.4rem;
  background: #f3f3f3;
  border: 1px solid hsl(240, 18%, 80%);
  padding: 0 1.0rem;
  font: 1rem Roboto; 
  cursor: pointer;
  transition: background-color 0.4s;
}

button:hover {
  background: #e9e7e7;
}

.top-contact {
    padding: 10px;
    height: 14rem;
    color: white;
    background-color: #084B8A;
    font:Roboto;
    /* width: 100%; */
}

.reservar {
    display: flex; 
    flex-direction: column; 
    align-content: center;
}

.menu {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 7px;
    font-family: 'Montserrat', sans-serif;
}

.menu a {
    display: table-cell;
    padding: 10px;
    text-decoration: none;
    color: black;
    font-size: 17px;
    transition: opacity 0.5s;
  }

  .menu a:hover {
    opacity: 0.6;
  }


@media (min-width: 800px) {
