    display: flex;
    padding: 10px;
    height: 40px;
    color: white;
    justify-content: space-between;
    background-color: #084B8A;
    font:Roboto;
    width: 100%;
}

.top-prime {
    display: flex;
    width: 500px;
}

.top-prime h3 {
    margin-top: -4px;
    padding: 10px;
    /* margin-left: -380px; */
}
.top-prime h1 {
    margin-top: -5px;
    text-align: left;
}

.top-contact h4 {
    margin-top: 0px;
    text-align: left;
    padding-right: 10px;
    padding-left: 10px;
}

.reservar {
    line-height:30px;
    justify-items: right;
    display: flex;
    flex-direction: row;
    margin-right: 25px;
